.interview {
  padding: 40px 0;
  @include small-tablet {
    padding: 20px 0;
  }
  &__video {
    height: 666px;
    border: 1px solid var(--seraya-obvodka);
    border-radius: 20px;
    box-shadow: 0 0 20px 0 rgba(156, 156, 156, 0.1);
    background: var(--seraya-obvodka);

    @include tablet {
      height: 450px;
    }
  }
}
